.header { 
    display: flex;
    padding: 2em;
    justify-content: space-between;
    margin-bottom: 1rem;
    color: black;
}

.header__left {
    flex: 0.2;
    color: black;
    display: flex;
    align-items: center;
    margin-right: 2em;
}

.header__center {
    flex:0.4;
    color: white;
    text-align: center;
    margin-left: 2em;
    margin-right: 2em;
}

.header__right {
    display: flex;
    flex: 0.2;
    align-items: center;
    color: white;
    text-align: center;
}

.header__right > h5 {
    margin-left: 2em;
    margin-right: auto;
}

.header__right > h5 > span {
    color: greenyellow;
}

.header__logo {
    margin-right: auto;
    object-fit: contain;
    max-width: 15vw;
}