@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap);

* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
}
.login {
    display: grid;
    place-items:center;
    height: 100vh;
    background-color: black;
}

.login > img {
    width: 60%;
}
h1 {
    color: white;
}
p { 
    color: white;
    padding: 1em;
}

.login__text {
    padding: 3em;
}
.login > a {
    padding: 20px;
    border-radius: 99px;
    background: #1db954;
    font-weight: 800;
    color: white;
    text-decoration: none;
}

.player__body {
    display: flex;
}
.sidebar {
    height: 100vh;
    flex: 0.1 1;
    color:white;
    padding-left: 10px;
    padding-right: 10px;
    max-width: 10vw;
    background-color: #000000;
    font-family: 'Poppins', sans-serif;
}

@media only screen and (max-width: 600px) {
    .sidebar {
display: none
    }
  }

.sidebar__heading {
    margin-right:auto;
    padding: 15px;
}

.sidebar__title {
    margin-left: 10px;
    padding: 5px;
    font-size: 12px;
}
.sidebarOption {
    display: flex;
    align-items: center;
    color: grey;
    height: 40px;
    cursor: pointer;
    transition: 200ms color ease-in;
    margin-right:auto;
    padding-left: 10px;
    padding-right: 10px;
}

.sidebar > hr {
    border: 1px solid #282828;
    width: 90%;
    margin: 10px auto;
}

.sidebarOption:hover {
    color:white;
}

.sidebarOption__icon {
    padding-left: 10px;
    padding-right: 10px;
}

.sidebarOption > p {
  margin-top: 10px;
  margin-left: 20px;
  font-size: 14px;
}
.body {
    flex: 1 1;
    height: 100vh;
    width: 90vw;
    overflow-y: overlay;
    background: black;
    background-size: 400% 400%;
    animation: AnimationName 34s ease infinite;
    align-items: center;
    font-family: 'Poppins', sans-serif;
}

@media only screen and (max-width: 600px) {
    .body {
      flex: 1 1;
      width: 100vw;
    }
  }
@keyframes AnimationName {
    0%{background-position:0% 51%}
    50%{background-position:100% 50%}
    100%{background-position:0% 51%}
}

h1 {
    color: black;
}
.count__text {
    color: black;
}
.time-sets {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    justify-items: center;
    align-items: center;
}
.grid-container {
    margin: 1em;
}

button {
    background: transparent;
    border: none;
}

button:hover,
a:hover {
  transition: transform 0.2s ease-in-out;
  transform: scale(1.2) !important;
}

a {
    text-decoration: none;
}

a:active, a:focus {
    outline: 0;
    border: none;
    -moz-outline-style: none;
  }
.header { 
    display: flex;
    padding: 2em;
    justify-content: space-between;
    margin-bottom: 1rem;
    color: black;
}

.header__left {
    flex: 0.2 1;
    color: black;
    display: flex;
    align-items: center;
    margin-right: 2em;
}

.header__center {
    flex:0.4 1;
    color: white;
    text-align: center;
    margin-left: 2em;
    margin-right: 2em;
}

.header__right {
    display: flex;
    flex: 0.2 1;
    align-items: center;
    color: white;
    text-align: center;
}

.header__right > h5 {
    margin-left: 2em;
    margin-right: auto;
}

.header__right > h5 > span {
    color: greenyellow;
}

.header__logo {
    margin-right: auto;
    object-fit: contain;
    max-width: 15vw;
}
.footer {
    position: fixed;
    display: flex;
    justify-content: space-between;
    bottom: 0;
    padding: 20px;
    height: 65px;
    width: 97%;
    background-color: #282828;
  }
  
  .footer__right * .MuiSlider-root {
    color: green;
  }
  .footer__left {
    display: flex;
    align-items: center;
    color: white;
    width: 300px;
    flex: 0.3 1;
  }
  
  .footer__green {
    color: #1ed15e;
  }
  
  .footer__center {
    flex: 0.4 1;
    max-width: 300px;
    padding: 0 100px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .footer__icon:hover,
  .footer__green:hover {
    transition: transform 0.2s ease-in-out;
    transform: scale(1.2) !important;
  }
  
  .footer__right {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    flex: 0.3 1;
  }
  
  .footer__albumLogo {
    height: 60px;
    width: 60px;
    margin-right: 20px;
    object-fit: contain;
  }
  
  .footer__songInfo > h4 {
    margin-bottom: 5px;
  }
  
  .footer__songInfo > p {
    font-size: 12px;
  }
